





















































































import Vue from 'vue';
import Component, { mixins } from 'vue-class-component';
import { formatTime } from '@/assets/lib/date';
import ControlMixin, { PositionItem } from '@/mixins/control';
import { Prop } from 'vue-property-decorator';

interface Option {
    label: string;
    value: string;
}

@Component({
    filters: { formatTime }
})
export default class Control extends mixins(ControlMixin) {
    @Prop() public readonly showPath!: boolean; // 是否显示轨迹

    public loading: boolean = false;
    public remoteMethod: ((key: string) => void) | null = null;
    public format: ((value: number) => string) | null = null; // 格式化 tooltip message
    public isPlaying: boolean = false; // 是否正在播放
    public options: Option[] = [];

    private timer?: number; // 播放定时器

    public get showPathProxy() {
        return this.showPath;
    }
    public set showPathProxy(visible: boolean) {
        this.$emit('update:showPath', visible);
    }

    public created() {
        this.format = (value: number) => formatTime(this.timeRange, value);

        let timer: number = 0;
        this.remoteMethod = (key: string) => {
            if (this.loading) {
                return;
            }

            clearTimeout(timer);
            if (!key) {
                return (this.options.length = 0);
            }

            timer = setTimeout(() => {
                this.loading = true;

                this.$http
                    .get('/api/tag/getall', {
                        pageSize: 100,
                        currentPage: 1,
                        name: key
                    })
                    .then(res => {
                        this.options.length = 0;
                        const icons = new Map<string, string>();
                        res.pagedData.datas.forEach(v => {
                            this.options.push({
                                label: v.name,
                                value: v.tagNo
                            });
                            icons.set(v.tagNo, v.photo);
                        });

                        this.$emit('set-icons', icons);
                        this.loading = false;
                    })
                    .catch(console.log);
            }, 500);
        };
    }
    public destroyed() {
        this.pause();
    }
    // 选择的标签及日期变化时的回调函数
    public change() {
        this.hasData = this.showPathProxy = false;
        this.progress = 0;
    }
    // 重播放
    public rePlay() {
        this.pause();
        Promise.resolve().then(this.play.bind(this));
    }
    public play() {
        if (this.isPlaying) {
            // ========================暂停
            this.pause(this.progress).$emit('pause');
        } else {
            if (this.hasData) {
                this.isPlaying = true;
                this._play();
            } else if (this.tagNos.length) {
                // =======================重新获取历史记录

                // 立即进入播放状态, 禁止修改时间及标签
                this.isPlaying = true;

                this.loadData()
                    .then(this._play.bind(this))
                    .catch((message: string) => {
                        message && this.$message.error(message);
                        this.isPlaying = false;
                    });
            } else {
                this.$message.warning('请选择标签');
            }
        }
    }

    // 暂停播放
    public pause(progress: number = 0) {
        if (this.timer) {
            cancelAnimationFrame(this.timer);
            this.timer = undefined;
        }
        this.progress = progress;
        this.isPlaying = false;

        return this;
    }

    private _play() {
        if (this.timer) {
            cancelAnimationFrame(this.timer);
            this.timer = undefined;
        }

        let time = Date.now();
        const onPlay = () => {
            const now = Date.now();
            if (this.isPlaying) {
                this.progress += ((now - time) / this.timeRange) * 100;
            }

            if (this.progress >= 100) {
                this.pause();
            } else {
                this.timer = requestAnimationFrame(onPlay);
            }

            time = now;
        };

        onPlay();
        this.$emit('play');
    }
}
