













































import Vue from 'vue';
import Component from 'vue-class-component';
import { Prop, Watch } from 'vue-property-decorator';

@Component
export default class Census extends Vue {
    @Prop() public readonly zones!: IZone[];
    @Prop() public readonly censusTags!: Map<string, Set<string>>;
    @Prop() public readonly censusChange!: number;

    public value: string = '';
    public allCount: number = 0; // 当前地图拥有的标签数
    public info: any = null;

    public created() {
        this.allCount = this.getAllCount();
    }

    @Watch('censusChange')
    @Watch('value')
    public getInfo() {
        if (this.zones && this.censusTags) {
            this.allCount = this.getAllCount();

            const zone = this.zones.find(v => v.baseNo1 === this.value);
            if (zone) {
                // this.$http
                //     .get('/api/tag/getall', {
                //         currentPage: 1,
                //         pageSize: 1_0000_0000,
                //         zone: zone.id
                //     })
                //     .then(res => {
                //         const list = (<ITag[]>res.pagedData.datas).filter(v =>
                //             (this.censusTags.get(this.value) || new Set()).has(
                //                 v.tagNo
                //             )
                //         );

                //         this.info = {
                //             name:
                //                 zone.name +
                //                 (zone.name.endsWith('区域') ? '' : '区域'),
                //             true: list.length,
                //             should: res.pagedData.datas.length
                //         };
                //     });
                this.info = {
                    name:
                        zone.name + (zone.name.endsWith('区域') ? '' : '区域'),
                    true: (this.censusTags.get(this.value) || new Set()).size
                };
            }

            this.info = null;
        }
    }

    private getAllCount() {
        return Array.from(this.censusTags.values()).flatMap(v => [...v]).length;
    }
}
